import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";

async function getAllBotos(token: string) {
  console.log("getAllBots", { token });
  return await HttpClient.request(
    `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/my/bots/`,
    token,
    HttpRequestMethod.GET
  );
}
function deleteBoto(token: string, botoId: number | null) {
  console.log("deleteBoto", { token, botoId });
  const response = HttpClient.request(
    `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${botoId}/`,
    token,
    HttpRequestMethod.DELETE,
    true
  );
}

export default { getAllBotos, deleteBoto };
