import AdminLayout from "@/layouts/Admin/index.vue";
import BotosRowPlaceHolder from "@/components/PlaceHolders/BotosRowPlaceHolder/index.vue";
import { Boto } from "@/interfaces/Boto";
import { defineComponent } from "vue";
import routes from "@/routes";
import Toggle from "@vueform/toggle";
import { mapActions, mapState } from "vuex";
import { useRoute } from "vue-router";
import BotoModal from "@/components/BotoModal/index.vue";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";
import { v4 as uuidv4 } from "uuid";

import apiClient from "./apiClient";

const items: Array<Boto> = [];

export default defineComponent({
  name: "Botos",
  components: {
    AdminLayout,
    BotosRowPlaceHolder,
    Toggle,
    BotoModal,
  },
  setup() {
    const route = useRoute();
    let bot = null;
    if (route.params.botId) {
      bot = {
        id: route.params.botId,
        name: route.params.botName,
        frequency: route.params.botFrequency,
        schedule: route.params.botSchedule,
        frequency_unit: route.params.botFrequencyUnit,
      };
    }
    return {
      bot,
    };
  },
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
      items,
      isLoadingData: false,
      errorText: "",
      error: false,
      activationModal: false,
      deletionModal: false,
      deletionLoading: false,
      activeItem: null as any,
      search: "",
      filteredItems: items,
      uid: uuidv4(),
      sortByNameOrder: 0,
      sortByIdOrder: 0,
      sortByStatusOrder: 0,
    };
  },
  computed: {
    ...mapState("auth", ["authToken"]),
    filteredList(): any {
      return this.items.filter((item: { name: string }) =>
        item.name.toLowerCase().includes(this.search.toLowerCase())
      );
    },
    activationRunningText(): string {
      if (this.bot && this.bot.schedule === "SCH") {
        const frequencyWithUnit = this.getFrequencyWithUnit(this.bot);
        return this.$t("botos.activation_periodic", {
          period: frequencyWithUnit,
        });
      } else {
        return this.$t("botos.activation_trigger");
      }
    },
  },
  created() {
    if (!navigator.onLine) {
      routes.push({
        name: "home.offline",
        params: { route: this.$route.fullPath },
      });
    }
  },
  async mounted() {
    // Get Blocks
    try {
      if (this.bot) {
        this.activationModal = true;
      }
      this.isLoadingData = true;
      await this.loadBotos();
    } catch (e) {
      this.error = true;
      this.errorText = HttpClient.getTextError(e);
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    ...mapActions("editor", ["setTemplate"]),
    async loadBotos() {
      const botos = await apiClient.getAllBotos(this.authToken);
      this.items = this.filteredItems = botos;
    },
    getFrequencyWithUnit(bot: any) {
      let unit = bot.frequency_unit || "MIN";
      let frequency;
      // TODO: enum
      switch (unit) {
        case "HR":
          frequency = bot.frequency / 60;
          unit = this.$t("global.hours");
          break;
        case "DAY":
          frequency = bot.frequency / 24 / 60;
          unit = this.$t("global.days");
          break;
        case "MIN":
          frequency = bot.frequency;
          unit = this.$t("global.minutes");
          break;
        default:
          frequency = bot.frequency;
          unit = this.$t("global.minutes");
      }
      return `${frequency} ${unit}`;
    },
    closeActivatedBotMessage() {
      this.activationModal = false;
    },
    cancelDeleteBot() {
      this.deletionModal = false;
      this.activeItem = null;
    },
    async deleteBoto() {
      try {
        this.deletionLoading = true;
        const item = this.getItemById(this.activeItem);
        // if there is no id, it makes no sense to delete
        if (item?.id) {
          await apiClient.deleteBoto(this.authToken, item.id);
          this.deletionModal = false;
          this.deletionLoading = false;
          this.activeItem = null;
          this.isLoadingData = true;
          await this.loadBotos();
          this.isLoadingData = false;
        }
      } catch (e) {
        // TODO
        console.error(e);
        this.deletionLoading = false;
        this.activeItem = null;
        this.deletionModal = false;
        this.isLoadingData = false;
      }
    },
    async switchBoto(id: number) {
      try {
        const item = this.getItemById(id);
        const status = item!.status;
        const name = item!.name;
        const formdata = new FormData();
        formdata.append("name", name);
        formdata.append("status", status);
        formdata.append("frequency", item!.frequency.toString());
        await HttpClient.request(
          `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${item!.id}/`,
          this.authToken,
          HttpRequestMethod.PUT,
          formdata,
          true
        );
      } catch (e) {
        // TODO
        console.error(e);
      }
    },
    sortByName() {
      if (this.sortByNameOrder === 0 || this.sortByNameOrder === -1) {
        this.filteredItems = this.filteredItems.sort((a: Boto, b: Boto) =>
          a.name.localeCompare(b.name)
        );
        this.sortByNameOrder = 1;
      } else {
        this.filteredItems = this.filteredItems.sort((a: Boto, b: Boto) =>
          b.name.localeCompare(a.name)
        );
        this.sortByNameOrder = -1;
      }
    },
    sortById() {
      if (this.sortByIdOrder === 0 || this.sortByIdOrder === -1) {
        this.filteredItems = this.filteredItems.sort(
          (a: Boto, b: Boto) => a.id! - b.id!
        );
        this.sortByIdOrder = 1;
      } else {
        this.filteredItems = this.filteredItems.sort(
          (a: Boto, b: Boto) => b.id! - a.id!
        );
        this.sortByIdOrder = -1;
      }
    },
    sortByStatus() {
      if (this.sortByStatusOrder === 0 || this.sortByStatusOrder === -1) {
        this.filteredItems = this.items.sort((a: Boto, b: Boto) =>
          a.status.localeCompare(b.status)
        );
        this.sortByStatusOrder = 1;
      } else {
        this.filteredItems = this.items.sort((a: Boto, b: Boto) =>
          b.status.localeCompare(a.status)
        );
        this.sortByStatusOrder = -1;
      }
    },
    deleteBotoDialog(id: number) {
      this.deletionModal = true;
      this.activeItem = id;
    },
    gotoBoto(id: number) {
      routes.push({ name: "home.editor", params: { id, uuid: uuidv4() } });
    },
    getItemById(id: number): Boto | undefined {
      return this.items.find((o: Boto) => o.id === id);
    },
    copyBoto(id: number) {
      const item = this.getItemById(id);
      item!.copy = true;
      item!.clone_of = id;
      item!.name = `${item!.name} ${this.$t("global.copy")}`;
      this.setTemplate(item);

      const v = uuidv4();
      // TODO: cache issues
      // this.$router.push(`/editor?${v}&copy=true`);
      this.$router.push({
        path: "editor",
        query: { templateId: id, type: "copy" },
      });
    },
  },
});
